import Constants from '../constants/ActionTypes'

import objectAssign from 'object-assign'

const initialState = {
  //uuid
    modalvisible:false,
    modalspin:true,
    downurl:"",
    downappname:"",
    downappcode:""
}

export default function task(state = initialState, action) {
  switch (action.type) {
    case Constants.DOAPPLYDOWN:
        return {...state,...action.value}
    
    default:
      return state
  }
}


