//****************************************
import React, { PropTypes, Component } from 'react'
import { bindActionCreators } from 'redux'
import { connect } from 'react-redux'
import * as DevActions from '../actions/Dev';
// import InitStore from '../reducers/Init';
//****************************************
import { Row,Col,Breadcrumb,Icon,Steps,Alert,Button,Form,Select,Input,Modal,Spin } from 'antd';
const createForm = Form.create;
const FormItem = Form.Item;
// const Option = Select.Option;

class Dev extends React.Component {
	constructor(props) { 
		super(props);

	}

	componentDidMount() {
	}
	handleReset(e) {
	   e.preventDefault();
	   this.props.form.resetFields();
	}
	handleSubmit(e) {
	    e.preventDefault();
	    this.props.form.validateFields((errors, values) => {
	      if (!!errors) {
	        console.log(errors);
	        return;
	      }
	      // let href="/cloudstore/system/ControlServlet.jsp?action=Action_DownLoadApp&name="+values.name
	      // window.location.href=href;
	      this.props.actions.setModalVisible(true);
	      this.props.actions.doApplyDown(values);

	    });
  	}
  	checkNull(rule,value,callback){
		const { getFieldValue,setFieldsValue } = this.props.form;
		if(value==''||value==null||value==undefined) {
				callback("");
		}
		callback();	
	}

	render() {
		const {init,actions} = this.props;
		const { getFieldProps, getFieldError, isFieldValidating ,getFieldsValue} = this.props.form;
	    const nameProps = getFieldProps('name', {
		      rules: [{
				validator:this.checkNull.bind(this)
			}],
	    });
	    return (
			<div>
				<Form horizontal >
					<Row style={{marginTop:"50",backgroundColor:"#ffffff",borderRadius:"15",height:"240"}}>
							<Row>
								<Col span={8} offset={4} style={{marginTop:"40"}}>
											<FormItem
											label=""
											labelCol={{ span: 8 }}
											wrapperCol={{ span: 14 }}
											style={{marginTop:'30'}}
											>
											    <Input {...nameProps} placeholder="" size="default" placeholder="" />
											</FormItem>
											
								</Col>
								<Col span={8} style={{marginTop:"40"}}>
											<FormItem wrapperCol={{ span: 20, offset: 3 }} style={{marginTop:'30'}}>
								        		<Button  type="primary" onClick={this.handleSubmit.bind(this)}></Button>
								          			&nbsp;&nbsp;&nbsp;&nbsp;
								        		<Button type="ghost" onClick={this.handleReset.bind(this)}></Button>
								        	</FormItem>
								</Col>
							</Row>
							<Row>
								<Col span={8} offset={4} style={{marginTop:"20"}}>
											<Col span={8} >
											</Col>
											<Col span={14}>
											   <a href={window.location.origin+"/cloudstore/libs4java/uuid/app/helper4developer.docx"} style={{'marginLeft':'0px'}}>/?</a>
											</Col>
								</Col>
							</Row>
					</Row>
					
				</Form>
				  <Modal title="" visible={this.props.dev.modalvisible}
			        onOk={()=>this.props.actions.setModalVisible(false)} 
			        onCancel={()=>this.props.actions.setModalVisible(false)}
			          okText="" cancelText=""
			       >
		       			<Spin spinning={this.props.dev.modalspin}>
			       			<div> 
					       		<p style ={{marginLeft:20,marginTop:20 , color:"#666"}}><label style ={{marginRight:20,color:"#666"}}>:</label>{this.props.dev.downappname}</p>
					            <p style ={{marginLeft:20,marginTop:20 , color:"#666"}}><label style ={{marginRight:20,color:"#666"}}>:</label>{this.props.dev.downappcode}</p>
					            <p style ={{marginLeft:20,marginTop:20 }}>
					            	<label style ={{marginRight:20,color:"#666"}}>:</label>
					            	<a href={this.props.dev.modalspin?"":this.props.dev.downurl}></a>
					            </p>
				            </div>
			            </Spin>
			      </Modal>
			</div>
		)
	}
};


//PropTypes
// Dev.propTypes = {
    // init: PropTypes.array.isRequired,
    // init: PropTypes.object.isRequired,
    // actions: PropTypes.object.isRequired
// }
Dev = createForm()(Dev);
Dev.propTypes = {
    dev: PropTypes.object.isRequired,
    actions: PropTypes.object.isRequired
}

function mapStateToProps(state) {
    return {
        dev: state.dev
    }
}

function mapDispatchToProps(dispatch) {
    return {
        actions: bindActionCreators(DevActions, dispatch)
    }
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(Dev)


