import React from 'react'
import { render } from 'react-dom'
import {combineReducers,applyMiddleware} from 'redux';
import { Provider, connect } from 'react-redux'

//import Provider from 'react-redux/lib/components/Provider'
//import connect from 'react-redux/lib/components/connect'

import configureStore from './store/configureStore'
import rootReducer from './reducers/index'
import thunkMiddleware from 'redux-thunk/lib/index'


//const ReactRouter = require('react-router');
//let { Router, Route, Link, useRouterHistory, IndexRedirect  } = ReactRouter;
import Router from 'react-router/lib/Router'
import Route from 'react-router/lib/Route'
import Link from 'react-router/lib/Link'
import useRouterHistory from 'react-router/lib/useRouterHistory'
import IndexRedirect from 'react-router/lib/IndexRedirect'

import { createHistory, useBasename, createHashHistory } from 'history'
//browserHistory
import { Breadcrumb, Menu, Icon } from 'antd';


import './style/index.css';

import Init from './components/Init';
import Auth from './components/Auth';
import AppDetail from './components/Apps/NewAppsDetail';
import RoleList from './components/RoleList';
import Role from './components/Role';
import NewApps from './components/Apps/NewApps';
import QueueAnim from 'rc-queue-anim';
import Home from './Home';
import Sys from './components/Sys';
import App from './components/Apps/App';
import AppWeaverMenu from './components/Apps/AppWeaverMenu';
import Dev from './components/dev';
import ImpExp from './components/ImpExp';
import Export from './components/ImpExpCom/Export';
import Import from './components/ImpExpCom/Import';

const store = configureStore(
    rootReducer,
    applyMiddleware(//dispatch
        thunkMiddleware
    )
);

store.subscribe(()=>{
	//console.log("redux,state",store.getState());
});


const browserHistory = useRouterHistory(createHashHistory)({
   
});

//browserHistory.basename="/cloudstore/system";
function requireCredentials(nextState, replace, next) {
  // console.log("nextstate",nextState);
  const query = nextState.location.query;
  // console.log("querytitle",query.title);
  nextState.routes[2].breadcrumbName = query.id+"  "+query.name;
  next();//
}

class Root extends React.Component {
    render() {
        return (
            <Provider store={store}>
           <Router history={browserHistory}>
                <Route name="home" breadcrumbName="" path="/" component={Home} ignoreScrollBehavior>
                    <Route name="newapps" breadcrumbName="" path="newapps" component={NewApps} />
                    <Route name="init" breadcrumbName="" path="init" component={Init} />
                    <Route name="auth" breadcrumbName="" path="auth" component={Auth} />
                    <Route name="roleList" breadcrumbName="" path="roleList">
                        <IndexRedirect to="roleList" />
                        <Route name="roleList" breadcrumbName="" path="roleList" component={RoleList}/>
                        <Route name="roleAdd" breadcrumbName="" path="add" component={Role} />
                        <Route name="roleEdit" breadcrumbName="" path=":id/edit" component={Role} />
                        <Route name="roleShow" breadcrumbName="" path=":id/show" component={Role} />
                    </Route>     
                    <Route name="sys" breadcrumbName="" path="sys" component={Sys} />
                    <Route name="dev" breadcrumbName="" path="dev" component={Dev} />
                    <Route name="impexp" breadcrumbName="" path="impexp">
                        <IndexRedirect to="impexp" />
                        <Route name="impexp" breadcrumbName="" path="impexp" component={ImpExp}/>
                        <Route name="export" breadcrumbName="" path="export" component={Export}/>
                        <Route name="import" breadcrumbName="" path="import" component={Import} />
                    </Route>
                </Route>
                <Route name="app" path=":id/set" component={App} />
                <Route name="app" path=":id/setwm" component={AppWeaverMenu} />
            </Router>
            </Provider>
        );
    }
};
//          <Route name="appApply" breadcrumbName=":kao" path=":id/show" component={AppDetail} onEnter={requireCredentials}/>




ReactDOM.render(<Root />, document.getElementById('container'));




