import { Form, Table, Button, Progress, Modal, Alert, Icon, Row, Col, Card, Tabs } from 'antd'
import { WeaTop, WeaInput4Wf } from 'weaCom'
import WeaInput4Page from './coms/WeaInput4Page.js'
import WeaInput4Theme from './coms/WeaInput4Theme.js'
const FormItem = Form.Item;
const TabPane = Tabs.TabPane;

import * as API from '../../api/ImpExp'

const columns = [{
	title: '',
	dataIndex: '',
	key: 'name',
	render(text, record, index) {
		return (
			<div>[{record.id}] {record.workflowname}</div>
		)
	}
}, {
	title: '',
	dataIndex: '',
	key: 'browser',
	render(text, record, index) {
		const { db_datashowset, xml_Browser } = record.counts;
		const count = parseInt(db_datashowset || 0) + parseInt(xml_Browser || 0);
		return (
			<div>{count}</div>
		)
	}
}, {
	title: 'SAP',
	dataIndex: '',
	key: 'sapbrowser',
	render(text, record, index) {
		const { db_intBrowserbaseInfo, xml_SapBrowser } = record.counts;
		const count = parseInt(db_intBrowserbaseInfo || 0) + parseInt(xml_SapBrowser || 0);
		return (
			<div>{count}</div>
		)
	}
}, {
	title: 'ACTION',
	dataIndex: '',
	key: 'action',
	render(text, record, index) {
		const { db_workflowactionset } = record.counts;
		const count = parseInt(db_workflowactionset || 0);
		return (
			<div>{count}</div>
		)
	}
}];


const columnsPortal = [{
	title: '',
	dataIndex: '',
	key: 'name',
	render(text, record, index) {
		return (
			<div>[{record.id}] {record.name}</div>
		)
	}
}];


class Main extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			wfData: {
				datas: [],
				details: [],
				loading: false,
				current: 0,
				url: ""
			},
			portalData:{
				elements:[],
				flows:[],
				pages:[],
				themes:[]
			},
			pageData: [],
			themeData: []
		}
	}

	componentDidMount() {		
        API.doGetPortalData().then((data) => {
			this.state.portalData.elements=data&&data.elements?data.elements:[];
			this.state.portalData.pages=data&&data.pages?data.pages:[];
			this.state.portalData.themes=data&&data.themes?data.themes:[];
		});
	}
	

	callback(key) {
		console.log(key);
	}

	render() {
		let that = this;
		const { history } = this.props;
		const { getFieldValue, getFieldProps } = this.props.form;
		const { wfData, loading, current, url,pageData,themeData } = this.state;
		const { datas, details } = wfData;
		return (
			<div className="wea-con" style={{ background: "url('/cloudstore/app/aa9bdf7ac9c794a09bb4640a0e541cfe6/images/brillant.png') repeat scroll 0 0 transparent" }}>
				<div>
					<Row style={{ fontSize: 22, fontWeight: 600, marginBottom: 20 }}>
						<Col span={20} offset={2}></Col>
					</Row>
					<Form horizontal>
						<Row>
							<Col span={20} offset={2}>
								<Card >
									<Tabs onChange={() => { this.callback.bind() }} type="line" className='io-tab'>
										<TabPane tab="" key="1">
											<FormItem label=""
												labelCol={{ span: 4 }}
												wrapperCol={{ span: 6 }}
												required>
												<WeaInput4Wf isMult={true} style={{ width: "100%" }}
													{...getFieldProps('wfId',
														{
															initialValue: "",
															getValueFromEvent: (value, valueSpan) => { that.doExportCheck(value, "wf"); return value; }
														})
													} />
											</FormItem>
											<FormItem label=""
												labelCol={{ span: 4 }}
												wrapperCol={{ span: 16 }}>
												<Table dataSource={details} columns={columns} size="small" />
											</FormItem>
										</TabPane>
										<TabPane tab="" key="2">
											<FormItem label=""
												labelCol={{ span: 4 }}
												wrapperCol={{ span: 6 }}
												required>
												<WeaInput4Page isMult={true} style={{ width: "100%" }}
													{...getFieldProps('pageId',
														{
															initialValue: "",
															getValueFromEvent: (value, valueSpan) => { that.doExportCheck(value, "page"); return value; }
														})
													} />
											</FormItem>
											<FormItem label=""
												labelCol={{ span: 4 }}
												wrapperCol={{ span:10 }}>
												<Table dataSource={pageData} columns={columnsPortal} size="small" />
											</FormItem>
											{/*<Col span={2}/>
											 <Col span={11}>
												<FormItem label=""
													labelCol={{ span: 6 }}
													wrapperCol={{ span: 10 }}
													required>
													<WeaInput4Theme isMult={true} style={{ width: "100%" }}
														{...getFieldProps('themeId',
															{
																initialValue: "",
																getValueFromEvent: (value, valueSpan) => { that.doExportCheck(value, "theme"); return value; }
															})
														} />
												</FormItem>
												<FormItem label=""
													labelCol={{ span: 6 }}
													wrapperCol={{ span: 16 }}>
													<Table dataSource={themeData} columns={columnsPortal} size="small" />
												</FormItem>
											</Col> */}
										</TabPane>
									</Tabs>
								</Card>
								<FormItem wrapperCol={{ span: 16 }} style={{ marginTop: 24 }}>
									<div>
										<Button type="primary" disabled={!(getFieldValue("wfId")||getFieldValue("pageId")||getFieldValue("themeId"))}
											onClick={this.doExport.bind(this)} loading={loading}></Button>
										<Button type="ghost" style={{ marginLeft: 15 }} onClick={() => { history.pushState(null, "/"); }}></Button>
									</div>
								</FormItem>
								<FormItem wrapperCol={{ span: 10 }} style={{ marginTop: 24 }}>
									<Progress percent={current} />
								</FormItem>
								{url &&
									<FormItem wrapperCol={{ span: 16 }} style={{ marginTop: 24 }}>
										<Alert
											message=""
											description={
												<a className="ant-form-text" href={url} target="_blank"></a>
											}
											type="success"
											showIcon
										/>

									</FormItem>}
								<FormItem wrapperCol={{ span: 16 }} style={{ marginTop: 24 }}>
									<Alert
										message=""
										description={
											<div>
												<p>1</p>
												<p>2SAPACTION</p>
											</div>
										}
										type="info"
										showIcon
									/>
								</FormItem>
							</Col>
						</Row>
					</Form>
				</div>
			</div>
		)
	}
	doExportCheck(value, type) {
		let that = this;
		if (type) {
			if (type === "wf") {
				API.doExportCheck({ wfId: value }).then((data) => {
					that.setState({
						wfData: data
					});
				});
			} else if (type === "page") {
				let idArr=value.split(",");			
				let temp=[];
				if(idArr.length>0){
					that.state.portalData.pages.map((data)=>{
						if(idArr.indexOf(data.id)>=0){
							temp.push(data);
						}
					});
				}
				that.setState({
					pageData:temp
				});								
			} else if (type === "theme"&&value!=="") {
				let idArr=value.split(",");	
				let temp=[];					
				if(idArr.length>0){
					that.state.portalData.themes.map((data)=>{
						if(idArr.indexOf(data.id)>=0){
							temp.push(data);
						}
					});
				}
				that.setState({themeData:temp});			
			}
		}

	}
	doExport() {
		const { getFieldValue } = this.props.form;
		let that = this;
		this.setState({
			loading: true,
			current: 0,
			url: ""
		});

		let current = 0;
		let run = setInterval(() => {
			if (current == 100) current = 99;
			that.setState({
				current: current++
			});
		}, 500);
		const wfId = getFieldValue("wfId");
		const pageId = getFieldValue("pageId");	
		let pageIdArr = pageId.split(",");
		let preId="";
		let aftId="";
		that.state.pageData.map((data)=>{
			if(pageIdArr.indexOf(data.id)>=0){
				if(data.type=="pre"){
					preId+=(data.id)+","
				}else if(data.type=="aft"){
					aftId+=(data.id)+","
				}
			}
		});
		preId=preId.substring(0,preId.length-1);
		aftId=aftId.substring(0,aftId.length-1);

		const themeId = getFieldValue("themeId");
		if(wfId!==""){
			API.doExport({ wfId: wfId}).then((msg) => {
				console.log("msg:", msg);
				if(msg.status&&msg.url&&msg.status==="true"){
					API.doExportAll({ flowPath: msg.url,preIds:preId,aftIds:aftId,themeIds:themeId }).then((msg) => {
						console.log("msg1:", msg);
						setTimeout(() => {
							that.setState({
								loading: false,
								current: 100,
								url: msg.zipFile					
							});
							window.clearInterval(run);
						}, 6000);
					});
				}
				
			});
		}else{
			API.doExportAll({ flowPath:"",preIds:preId,aftIds:aftId,themeIds:themeId }).then((msg) => {
				console.log("msg2:", msg);
				setTimeout(() => {
					that.setState({
						loading: false,
						current: 100,
						url: msg.zipFile					
					});
					window.clearInterval(run);
				}, 3000);
			});
		}
		
		
	}
}

Main = Form.create()(Main);

export default Main


