export const doApplyDown = (value) =>{
	return (dispatch, getState) =>{
		API.doApplyDownP(value).then(data =>{
			dispatch(reducerApplyDown(data));
		});
	}
}

function reducerApplyDown(datas) {
  return {
    type: "DOAPPLYDOWN",
    value:{
    	modalspin:!datas.status,
	    downurl:datas.url,
	    downappname:datas.appname,
	    downappcode:datas.appcode,
    }
   
  }
}

export const setModalVisible= (value) =>{
	return (dispatch) =>{
			dispatch(reducerModalVisible(value));
	}
}


function reducerModalVisible(value) {
  return {
    type: "DOAPPLYDOWN",
    value:{
    	modalvisible:value,
    }
  }
}




