import {Form,Input,Modal,Button,Icon,Col,Tabs,Badge,Tag,Tooltip,Card,Row} from 'antd'
import classNames from 'classnames';
import {WeaInputSearch,WeaSearchTable} from 'weaCom'
import PortalTree from "../portal-tree/index"

const FormItem = Form.Item;
const InputGroup = Input.Group;
const TabPane = Tabs.TabPane;

class PortalInput4Base extends React.Component {

	static defaultProps={
		searchUrl : "/api/ec/api/hrm/search",
	}
	static propTypes={
		seacrhPara:React.PropTypes.string,
		isMult:React.PropTypes.bool,
	}
	constructor(props) {
		super(props);
		const {value, valueSpan} = props;
		this.state = {
			visible: false,
			ids: value ? value : "",
			names: valueSpan ? valueSpan : "",
			value: '',
      		focus: false,
      		hasSearchValue:false,
      		seacrhPara:'',
		}
	}
	componentWillReceiveProps(nextProps) {
		if (this.props.value !== nextProps.value && this.props.valueSpan !== nextProps.valueSpan) {
			//console.log("this.props.value:",this.props.value);
			//console.log("nextProps.value:",nextProps.value);
			this.setState({
				ids: nextProps.value,
				names: nextProps.valueSpan
			});
		}
	}
	shouldComponentUpdate(){
		// console.log('shouldComponentUpdate');
		return true
	}
	showModal=()=> {
		this.setState({
			visible: true
		});
	}
	hiddenModal=()=>{
		this.handleOk();
	}
	handleOk() {
		this.setState({
			visible: false
		});
		const {ids, names} = this.state;
		if (typeof (this.props.onChange) == "function") {
			this.props.onChange(ids, names);
		}
	}
	handleCancel() {
		this.setState({
			visible: false
		});
	}
	handleClear() {
		this.setState({
			visible: false,
			ids: "",
			names: ""
		});
		if (typeof (this.props.onChange) == "function") {
			this.props.onChange("", "");
		}
	}
    onSearchChange = (v)=>{//
    	const {hasSearchValue}=this.state;
        v = encodeURI(v);
        setTimeout(()=>{
            this.setState({
               seacrhPara:`&lastname=${v}`,
            });
            if(v || v!=''){//
                this.setState({hasSearchValue:true})
            }else{
                this.setState({hasSearchValue:false})
            }
        },50);
    }
	render() {
		//console.log("this.props:",this.props);
		const {visible,hasSearchValue,seacrhPara,focus} = this.state;
		let {ids, names} = this.state;
		const {searchUrl,singleGroup, id, name, width, style, title, topPrefix, topIconUrl, dataUrl, 
			paramsUrl, dataKey, otherPara, isMult, ifAsync, ifCheck, disabled, prefix, value, valueSpan, modalWidth, et,
		} = this.props;
		let {browerType} = this.props;
		// console.log("browerType:",browerType);
		// console.log('isMult',isMult,'ids',ids,'names',names);
		if (!browerType) browerType = ['tree'];
		//console.log("browerType.indexOf(list)>=0:",browerType.indexOf("list")>=0);
		//console.log("value1:",value," valueSpan:",valueSpan);
		if (value && value != "" && (ids == "" || !ids)) {
			ids = value;
			names = valueSpan;
		}

		ids = ids ? ids.toString() : "";
		names = names ? names.toString() : "";
		//console.log("ids  :",ids);
		const idArr = ids ? ids.split(",") : [];
		const nameArr = ids ? names.split(",") : [];
		let tabHtml = "";
		if (ids != "") {
			tabHtml = <Badge count={idArr.length}></Badge>;
		}
		else {
			tabHtml = "";
		}
		let nameHtmlArr = new Array();
		for (let i = 0; i < idArr.length && ids != ""; i++) {
			const tid = idArr[i];
			const tname = nameArr[i];
			nameHtmlArr[i] = <Tag key={i} color="blue">{tname}</Tag>;
		}
		//let buttonClass = "ant-search-btn ant-input-group-formitem";
		

		let tabPaneArr = [],searchTablePane = [];
		const isTree = browerType.indexOf("tree") >= 0;
		if (isTree) {
			tabPaneArr[tabPaneArr.length] = (
				<TabPane tab="" key="tree">
					<div style={{ "height": "490px", "overflow": "auto" }}>
						<PortalTree
							id={id}
							topPrefix={topPrefix}
							prefix={prefix}
							topIconUrl={topIconUrl}
							dataUrl={dataUrl}
							dataKey={dataKey}
							otherPara={otherPara}
							isMult={isMult}
							ifAsync={ifAsync}
							ifCheck={ifCheck}
							setData={this.setData.bind(this) }
							ids={ids}
							names={names}
							/>
					</div>
				</TabPane>
			);
			searchTablePane[searchTablePane.length]=(<TabPane tab="" key="tree"></TabPane>);

		}
		if (singleGroup) {
			tabPaneArr[tabPaneArr.length] = (
				<TabPane tab="" key="group">
					<div style={{ 'height': '490px', 'overflow': 'auto' }}>

						<PortalTree
							id={id}
							topPrefix={"group"}
							prefix={"groupry"}
							topIconUrl={topIconUrl}
							dataUrl={"/cloudstore/system/ControlServlet.jsp"}
							dataKey={"id"}
							otherPara={["action", "Action_GetGroupDataList"]}
							isMult={isMult}
							ifAsync={ifAsync}
							ifCheck={ifCheck}
							setData={this.setData.bind(this) }
							ids={ids}
							names={names}
							/>
					</div>
				</TabPane>
			);
			searchTablePane[searchTablePane.length]=(<TabPane tab="" key="group"></TabPane>);
		}
		if (browerType.indexOf("list") >= 0) {
			const {ht, t, dt, listUrl, countUrl, datasType, dn, keyNum} = this.props;
			//console.log("base:",dn," keyNum:",keyNum);
			tabPaneArr[tabPaneArr.length] = (
				<TabPane tab="" key="list">
					<WeaList
						id={id}
						fieldId={this.props.fieldId}
						paramsUrl={paramsUrl}
						listUrl={listUrl}
						countUrl={countUrl}
						fieldhtmltype={ht}
						fielddbtype={dt}
						type={t}
						isMult={isMult}
						setData={this.setData.bind(this) }
						ids={ids}
						names={names}
						datasType={datasType}
						dn={dn}
						keyNum={keyNum}
						visible={visible}
						ondblclickCallBack={this.hiddenModal}
						/>
				</TabPane>
			);
			searchTablePane[searchTablePane.length]=(<TabPane tab="" key="list"></TabPane>);
		}
		const  selectedTab = (
			<TabPane tab={tabHtml} key="isUse">
				<div style={{ "height": "490px", "overflow": "auto" }}>
					{nameHtmlArr}
				</div>
			</TabPane>
		)
		tabPaneArr[tabPaneArr.length] = selectedTab;
		searchTablePane[searchTablePane.length] = selectedTab;
		//console.log("style:",style);
		//console.log("names:",names);
		let inputArr = (
			<div className="ant-search-custom">
				<Input key="theInput" style={style} addonAfter={<div style={{ "cursor": "pointer" }}>
					<i onClick={this.showModal.bind(this) } className="anticon anticon-search" ></i>
				</div>} value={names} title={names}/>
			</div>
		);
		// if (ids.length > 0) {
		// 	inputArr = (<Tooltip title={names}>{inputArr}</Tooltip>)
		// }
		if (et && et == 2 && ids.length == 0) {
			inputArr = (<Badge dot>{inputArr}</Badge>)
		}
		const isHrm =  (typeof prefix === 'string' && prefix.indexOf('ry') === 0) || false;
		// console.log('visible',visible,'hasSearchValue',hasSearchValue);
		return (
			<div>
				{inputArr}
				<input type="hidden" id={id} name={name} value={ids} />
				<Modal className="wea-browser-old"
					wrapClassName="vertical-center-modal"
					title={title}
					visible={visible}
					onCancel={this.handleCancel.bind(this) }
					width={modalWidth}
					footer={[
						<Button  key="submit" type="primary" size="small" onClick={this.handleOk.bind(this) }> </Button>,
						<Button  key="back" type="ghost" size="small" onClick={this.handleClear.bind(this) }> </Button>,
						<Button key="cancle" type="ghost" size="small" onClick={this.handleCancel.bind(this) }></Button>
					]}
					>
					<p></p><img src="/cloudstore/images/store/mnav2_wev8.png"  alt="bumen" /><span>{title}</span>
					{isHrm && 
						<WeaInputSearch style={{width:'100%'}}
							onFocusChange={this.onFocusChange.bind(this)} 
							onSearchChange={this.onSearchChange}
						 />
					}
					{!hasSearchValue ? 
					  (<Tabs defaultActiveKey={browerType[0]} style={{ "margin-left": "10px", "margin-right": "10px" }}>
					  {tabPaneArr} 
					  </Tabs>)
					: (<div>
						{/*<Tabs defaultActiveKey={browerType[0]} style={{ "margin-left": "10px", "margin-right": "10px" }}>
					     {searchTablePane} 
					    </Tabs>*/}
					    <div style={{height:'490',overflowY:'scroll'}}>
						  <WeaSearchTable 
						  	 isMult={isMult}
							 isaccount={true}
							 url={searchUrl} 
	                    	 seacrhPara={seacrhPara} 
	                    	 min={1} 
	                    	 max={10} 
	                    	 setData={this.setData.bind(this)}
	                    	 onRowDoubleClick={this.onRowDoubleClick.bind(this)}
	                    	 onChange={this.onCheckChange.bind(this)}
	                      />
                        </div>
                       </div>
                      )
					}
				</Modal>
			</div>
		);
	}
	setData(ids, names) {
		console.log("ids:",ids,"names:",names);
		if (typeof (this.props.onChange) == "function") {
			this.props.onChange(ids, names);
		}
		this.setState({
			ids: ids,
			names: names,
		});;

	}
	onRowDoubleClick(record){
		// console.log('onRowDoubleClick==record',record)
		this.setState({
			ids: record.key,
			names: record.lastname,
			visible:false,
		});;
	}
	onCheckChange(record){
		// console.log('onCheckChange==record',record);
		let keyArrCach = [],namesArrCach = [];
		record.forEach((m,i)=>{
			keyArrCach.push(m.key);
			namesArrCach.push(m.lastname);
		})
		this.setState({
			ids:keyArrCach,
			names:namesArrCach,
		})
	}
};

export default PortalInput4Base;




