import { Form, Table, Button, Progress, Modal, Alert, Icon, Row, Col, Card, Tabs } from 'antd'
import PortalInput4Base from './portal-input4-base/index'
class WeaInput4Theme extends React.Component {
	shouldComponentUpdate(nextProps, nextState) {
		return nextProps.value !== this.props.value;
	}
	render() {
		return (
			<PortalInput4Base 
				title=""
				topPrefix=""
				prefix=""
				topIconUrl="/cloudstore/images/icon/treeTop.gif"
				dataUrl="/cloudstore/system/ControlServlet.jsp"
				dataKey="id"
				otherPara={["action","Action_ImportAndExport","select","theme"]}
				ifAsync={true}
				ifCheck={true}
				{...this.props} 
			/>
		)
	}
}

export default WeaInput4Theme;



