import Link from 'react-router/lib/Link'
import {Card,Row,Col,Radio,Form,Alert} from 'antd'

import {WeaTop} from 'weaCom'

import * as API from '../api/ImpExp'
import './ImpExpCom/index.css'
const RadioButton = Radio.Button;
const RadioGroup = Radio.Group;
const FormItem = Form.Item;

class ImpExp extends React.Component {
	constructor(props) {
		super(props);
	}
	render() {
		return (
			<div style={{ marginTop:'10px'}}>
				<div className="code-box-demo" style={{ background: '#ECECEC', padding: '30px' }}>
					<Row>
						<Col span="12">
							<Card title="" bordered={false}>
								<Link to="/impexp/export"></Link>
							</Card>
						</Col>
						<Col span="12">
							<Card title="" bordered={false}>
								<Link to="/impexp/import"></Link>
							</Card>
						</Col>
					</Row>
				</div>
			</div>
		)
	}
}

export default ImpExp


