import {Form,Table,Button,Progress,Modal,Alert,Icon,Row,Col,Card,Tabs} from 'antd'
import {WeaTop,WeaInput4SapDs} from 'weaCom'

import { Upload, message,Radio } from 'antd';

const FormItem = Form.Item;
const RadioGroup = Radio.Group;
const TabPane = Tabs.TabPane;

import * as API from '../../api/ImpExp'

const columns = [{
  title: '',
  dataIndex: '',
  key: 'name',
  render(text,record,index) {
  	return (
  		<div>[{record.id}] {record.workflowname}</div>
  	)
  }
}, {
  title: '',
  dataIndex: '',
  key: 'browser',
  render(text,record,index) {
  	const {db_datashowset,xml_Browser} = record.counts;
  	const count = parseInt(db_datashowset||0)+parseInt(xml_Browser||0);
  	return (
  		<div>{count}</div>
  	)
  }
}, {
  title: 'SAP',
  dataIndex: '',
  key: 'sapbrowser',
  render(text,record,index) {
  	const {db_intBrowserbaseInfo,xml_SapBrowser} = record.counts;
  	const count = parseInt(db_intBrowserbaseInfo||0)+parseInt(xml_SapBrowser||0);
  	return (
  		<div>{count}</div>
  	)
  }
}, {
	title: 'ACTION',
	dataIndex: '',
	key: 'action',
	render(text,record,index) {
		const {db_workflowactionset} = record.counts;
		const count = parseInt(db_workflowactionset||0);
		return (
			<div>{count}</div>
		)
	}
}];

const columnsPortal = [{
	title: '',
	dataIndex: '',
	key: 'name',
	render(text, record, index) {
		return (
			<div>[{record.id}] {record.name}</div>
		)
	}
}];

class Main extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			data:{
				datas:[],
				details:[]
			},
			loading:false,
			current:0,
			url:"",
			checkKeys:[],

			pageData:[],
			checkKeys4Page:[],
			pagePath:"",
		}
	}
	render() {
		
		let that = this;
		const {history} = this.props;
		const {getFieldValue,getFieldProps} = this.props.form;
		let {data,loading,current,url,checkKeys,pageData,checkKeys4Page} = this.state; 
		let {datas,details} = data;
		let uploadProps = {
		  	name:"zipFile",
			showUploadList:true,
			action:'/api/import/export/check',
			data:{},
			onChange(info) {
				console.log("info:",info);
				const {status,response} = info.file;
				if(status=="done"&&response.status=="true") {
					console.log("status:",status," response:",response);					
					that.setState({
						url:response.flowPath,
						pageData:response.portalPage
					});
					let tempXmlPath="";				
					that.state.pageData.map((v)=>{
						if(v&&v.xmlPath){
							tempXmlPath+=(v.xmlPath+",");
						}
					});
					tempXmlPath!==""?tempXmlPath=tempXmlPath.substring(0,tempXmlPath.length-1):"";	
					that.setState({
						pagePath:tempXmlPath,
					});
					console.log("---that.state:",that.state);
					that.doImportCheck(that.state.url);
				}
			}
		};

	// 	const uploadProps = {
	// 		name:"zipFile",
	// 	  showUploadList:true,
	// 	  action:'/cloudstore/system/ControlServlet.jsp?action=Action_DoDiffXmlImport',
	// 	  data:{},
	// 	  onChange(info) {
	// 		  console.log("info:",info);
	// 		  const {status,response} = info.file;
	// 		  console.log("status:",status," response:",response);
	// 		  if(status=="done"&&response.status=="true") {
	// 			  that.setState({
	// 				  url:response.url
	// 			  });
	// 			  that.doImportCheck(response.url);
	// 		  }
	// 	  }
	//   };

		const rowSelection = {
	      	checkKeys,
	      	onChange(selectedRowKeys) {
	      		console.log("selectedRowKeys:",selectedRowKeys);
	      		that.setState({
	      			checkKeys:selectedRowKeys
	      		})
	      	}
		};
		
		const rowSelectionPage = {
			checkKeys4Page,
			onChange(selectedRowKeys) {
				console.log("selectedRowKeys4Page:",selectedRowKeys);
				that.setState({
					checkKeys4Page:selectedRowKeys
				})
			}
		  };
		//console.log("url:",url);
		return (
			<div className="wea-con" style={{background:"url('/cloudstore/app/aa9bdf7ac9c794a09bb4640a0e541cfe6/images/brillant.png') repeat scroll 0 0 transparent"}}>
				<div>
				<Row  style={{fontSize:22,fontWeight:600,marginBottom:20}}>
						<Col span={20} offset={2}></Col>
				</Row>
				<Form horizontal>
					<Row>
						<Col span={20} offset={2}>
							
							<Card >								
								{/* <FormItem label=""
									labelCol={{ span: 4 }}
									wrapperCol={{ span: 16 }}
									required>
									<RadioGroup {...getFieldProps("consistent",{
													initialValue:1
												})}
									>
										<Radio key="yes" value={1}></Radio>
										<Radio key="no" value={0}></Radio>
									</RadioGroup>
								</FormItem> */}
								<FormItem 							
									wrapperCol={{ span: 16 }}
									required>
									<Upload {...uploadProps}>
										<Button type="ghost">
										<Icon type="upload" /> 
										</Button>
									</Upload>
								</FormItem>
								<Tabs type="line" className='io-tab'>
									<TabPane tab="" key="1">
										<FormItem label=""
											labelCol={{ span: 4 }}
											wrapperCol={{ span: 16 }}
											required>
											<Table  dataSource={details} columns={columns} rowSelection={rowSelection} rowKey={record => record.id} size="small" />
										</FormItem>
										{this.haveSapBrowser() &&
											(
												<FormItem label="SAP"
												labelCol={{ span: 4 }}
												wrapperCol={{ span: 6 }}
												required>
													<WeaInput4SapDs {...getFieldProps('sapDs',{initialValue:""})} />
												</FormItem>
											)
										}
									</TabPane>
									<TabPane tab="" key="2">
										<FormItem label=""
											labelCol={{ span: 4 }}
											wrapperCol={{ span: 10 }}
											required>
											<Table  dataSource={pageData} columns={columnsPortal} rowSelection={rowSelectionPage} rowKey={record => record.id} size="small" />
										</FormItem>
										{/* <Col span={2}/>
										<Col span={11}>	
										<FormItem label=""
											labelCol={{ span: 6 }}
											wrapperCol={{ span: 16 }}
											required>
											<Table  dataSource={details} columns={columnsPortal} rowSelection={rowSelection} rowKey={record => record.id} size="small" />
										</FormItem>
										</Col> */}
									</TabPane>
								</Tabs>
							</Card>
					        <FormItem wrapperCol={{ span:16 }} style={{ marginTop: 24 }}>
					        	<div>
					        		<Button type="primary" disabled={(checkKeys.length===0&&checkKeys4Page.length===0 )|| (this.haveSapBrowser()&&!getFieldValue("sapDs"))} 
					        			onClick={this.doImport.bind(this,url)} loading={loading}></Button>
					        		<Button type="ghost" style={{marginLeft:15}} onClick={()=>{history.pushState(null,"/");}}></Button>
					        	</div>
					        </FormItem>
					        <FormItem wrapperCol={{ span:10}} style={{ marginTop: 24 }}>
					        	<Progress percent={current} />
					        </FormItem>
					        {url && 
					        <FormItem wrapperCol={{ span:16 }} style={{ marginTop: 24 }}>
					        	<Alert
								    message=""
								    description={
								    	<div>
								    		<p></p>
								    	</div>
								    }
								    type="info"
								    showIcon
								  />
					        </FormItem>}
					        <FormItem wrapperCol={{ span:16 }} style={{ marginTop: 24 }}>
					        	<Alert
								    message=""
								    description={
								    	<div>
								    	<p>1</p>
								    	<p>2SAP</p>
								    	</div>
								    }
								    type="info"
								    showIcon
								  />
					        </FormItem>
						</Col>
						<Col span={8} offset={4} style={{paddingTop:30}}>
						</Col>
					</Row>
				</Form>
				</div>
			</div>
		)
	}
	doImportCheck(url) {
		let that = this;
		API.doImportCheck({path:url}).then((data)=>{
			console.log("data:",data);
			that.setState({
				data:data
			});
		});
	}
	doImport(url) {
		let {checkKeys,checkKeys4Page} = this.state;
		const {getFieldValue} = this.props.form;
		let that = this;
		this.setState({
			loading:true,
			current:0,
			url:""
		});

		let current = 0;
		let run = setInterval(()=>{
			if(current==100) current = 99;			
			that.setState({
				current:current++
			});
			
		},500);
		API.doImport({path:url,wfId:checkKeys.join(","),sapds:getFieldValue("sapDs"),consistent:getFieldValue("consistent")}).then((msg)=>{
			API.doImportAll({pagePath:that.state.pagePath}).then((msg)=>{
					setTimeout(()=>{
						that.setState({
							loading:false,
							current:100,
							url:msg.url
						});
						window.clearInterval(run);
						that.doImportCheck(url);
					},3000);
				}
			);
			
		});
	}
	haveSapBrowser() {
		let find = false;
		const {details} = this.state.data;
		for(let i=0;i<details.length&&!find;i++) {
			const counts = details[i].counts;
			if(counts.db_intBrowserbaseInfo>0||counts.xml_SapBrowser>0) {
				find = true;
			}
		}
		return find;
	}
}

Main = Form.create()(Main);

export default Main



